import { useEffect, useState } from "react"

import Header from "./Header"

import { TimePicker } from '@material-ui/pickers'
import DateFnsUtils from '@date-io/date-fns'
import { MuiPickersUtilsProvider } from '@material-ui/pickers'
import Button from '@mui/material/Button'

import Table from '@mui/material/Table'
import TableBody from '@mui/material/TableBody'
import TableCell from '@mui/material/TableCell'
import TableContainer from '@mui/material/TableContainer'
import TableHead from '@mui/material/TableHead'
import TableRow from '@mui/material/TableRow'
import Paper from '@mui/material/Paper'

import CONSTANTS from "../utils/constants"

import DialogForm from "./DialogForm"

import '../styles/MainSection.css'

const HourTable = ({ startTime, endTime, handleStartClockChange, handleEndClockChange }) => {
  return (
    <div className="tableContainer">
      <TableContainer component={Paper}>
        <Table aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell><strong>Dia</strong></TableCell>
              <TableCell><strong>Hora de llagada</strong></TableCell>
              <TableCell><strong>Hora de salida</strong></TableCell>
            </TableRow>
          </TableHead>
        <TableBody>
          { CONSTANTS.NAME_DAYS.map(day => 
            <TableRow key={ day }>
              <TableCell>{ day }</TableCell>
              <TableCell>
                <div>
                  <MuiPickersUtilsProvider utils={ DateFnsUtils }>
                    <TimePicker value={ startTime[day] } onChange={ event => handleStartClockChange(event, day) } />
                  </MuiPickersUtilsProvider>
                </div>
              </TableCell>
              <TableCell>
                <div>
                  <MuiPickersUtilsProvider utils={ DateFnsUtils }>
                    <TimePicker value={ endTime[day] } onChange={ event => handleEndClockChange(event, day) } />
                  </MuiPickersUtilsProvider>
                </div>
              </TableCell>
            </TableRow>
          ) }
        </TableBody>
      </Table>
      </TableContainer>
    </div>
  )
}

const ResultTable = ({ totalTime, formatHour, formatMinutes, amountPerDay }) => {
  return (
    <div className="tableContainer">
      <TableContainer component={Paper}>
        <Table aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell><strong>Dia</strong></TableCell>
              <TableCell><strong>Tiempo</strong></TableCell> 
              <TableCell><strong>Sueldo dia</strong></TableCell> 
            </TableRow>
          </TableHead>
          <TableBody>
          { CONSTANTS.NAME_DAYS.map(day => 
          <TableRow key={ day }>
            <TableCell>{ day }</TableCell>
            <TableCell>{ (amountPerDay[day] === 0 ? '-' : formatHour(totalTime[day]) + ' ' + formatMinutes(totalTime[day])) }</TableCell>
            <TableCell>{ (amountPerDay[day] === 0 ? '-' : '$' + amountPerDay[day]) }</TableCell>
          </TableRow>
          ) }
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  )
}

const MainSection = () => {
  let scheduleTimesStartWeek = {}
  let scheduleTimesEndWeek = {}
  let scheduleDisableDays = {}

  const [startTime, setStartTime] = useState({})
  const [endTime, setEndTime] = useState({})
  const [totalTime, setTotalTime] = useState({})
  const [disableDays, setDisableDays] = useState({})
  const [amountPerDay, setAmountPerDay] = useState({})
  const [salary, setSalary] = useState(CONSTANTS.SALARY_BASE)
  const [openDialogForm, setOpenDialogForm] = useState(false)

  useEffect(() => {
    CONSTANTS.NAME_DAYS.forEach(day => scheduleTimesStartWeek[day] = new Date(CONSTANTS.DEFAULT_START_TIME))
    CONSTANTS.NAME_DAYS.forEach(day => scheduleTimesEndWeek[day] = new Date(CONSTANTS.DEFAULT_END_TIME))
    CONSTANTS.NAME_DAYS.forEach(day => scheduleDisableDays[day] = false)
    setStartTime(scheduleTimesStartWeek)
    setEndTime(scheduleTimesEndWeek)
    setDisableDays(scheduleDisableDays)
  }, [])

  useEffect(() => calculateMinutesBetweenDaysAndAmount(), [startTime, endTime, salary])

  const handleStartClockChange = (event, day) => setStartTime({ ...startTime, [day]: event })
  const handleEndClockChange = (event, day) => setEndTime({ ...endTime, [day]: event })

  const calculateMinutesBetweenDaysAndAmount = () => {
    let scheduleWeek = { ...amountPerDay }
    let totalAmountPerDay = { ...totalTime }
    let timeBetweenDates = 0

    CONSTANTS.NAME_DAYS.forEach(day => {
      if (!disableDays[day]) {
        timeBetweenDates = Math.abs(startTime[day] - endTime[day])
        scheduleWeek[day] = Math.floor((timeBetweenDates / CONSTANTS.MILISECONDS_IN_SECOND) / CONSTANTS.SECONDS_IN_MINUTE)
        totalAmountPerDay[day] = (salary / CONSTANTS.MINUTES_IN_HOUR) * scheduleWeek[day]
      }
    })
    setTotalTime(scheduleWeek)
    setAmountPerDay(totalAmountPerDay)
  }

  const handleCahngeSalary = () => setOpenDialogForm(true)

  const formatHour = (minutes) => {
    const hours = (minutes - (minutes % CONSTANTS.MINUTES_IN_HOUR)) / CONSTANTS.MINUTES_IN_HOUR
    return hours !== 0 ? hours + ' hrs' : ''
  }

  const formatMinutes = (minutes) => {
    const totalMinutes = minutes % CONSTANTS.MINUTES_IN_HOUR 
    return totalMinutes !== 0 ? totalMinutes + ' min' : ''
  }

  const calculateTotalTime = () => {
    let total = 0

    CONSTANTS.NAME_DAYS.forEach(day => !disableDays[day] && (total += totalTime[day]))
    return formatHour(total) + ' ' + formatMinutes(total)
  }

  const calculateTotalSalary = () => {
    let total = 0

    CONSTANTS.NAME_DAYS.forEach(day => !disableDays[day] && (total += amountPerDay[day]))
    return '$' + total
  }

  return (
    <div>
      <Header />
      <HourTable 
        startTime={startTime}
        endTime={endTime}
        handleStartClockChange={handleStartClockChange}
        handleEndClockChange={handleEndClockChange}
      />
      { isNaN(calculateTotalTime(totalTime)) && 
        <ResultTable 
          calculateTotalTime={calculateTotalTime}
          totalTime={totalTime}
          formatHour={formatHour}
          formatMinutes={formatMinutes}
          amountPerDay={amountPerDay}
        /> 
      }
      <div className="resultContainer">
        { isNaN(calculateTotalTime(totalTime)) && <b>Tiempo total: {calculateTotalTime()}</b> }
      </div>
      <div className="resultContainer">
        { isNaN(calculateTotalTime(totalTime)) && <b>Sueldo total: {calculateTotalSalary()}</b> }
      </div>
      <DialogForm open={ openDialogForm } handleClose={ () => setOpenDialogForm(false) } changeSalary={ (salary) => setSalary(salary) } />
      <Button variant="contained" onClick={ handleCahngeSalary } style={{marginTop:15}}>${salary} la hora</Button>
    </div>
  )
}

export default MainSection